import request from '@/utils/request'
//获取所有比赛
export function getAllEvents(){
    return request({
        url:'/api/v1/Events',
        method:'get'
    })
}
//发布新赛事
export function pushNewEvents(name,is_over,opentime){
    return request({
        url:'/api/v1/admin/Events',
        method:'post',
        params:{
          name,
          is_over,
          opentime
        }
    })
}
//删除
export function deleteEvents(name){
  return request.delete(`/api/v1/admin/Events?event_name=${name}`)
}
//更新
export function updateEvents(event_name,row){
  return request({
    url:'/api/v1/admin/Events',
    method:'put',
    params:{
      event_name
    },
    data:{
      id:row.id,
      name:row.name,
      is_over:row.is_over,
      opentime:row.opentime
    }
  })
}